<template>
    <div class="detail">
        <!-- 回到顶部 -->
        <div class="goback-top">
            <el-backtop  :bottom="100" :right="100">
                <div>UP</div>
            </el-backtop>
       </div>

        <div class="head-content">
            <div class="head-box">
                    <div class="head-content clearfix">
                        <div class="left-cont fl">
                                <div class="logo-box" @click="goMain">
                                    <img class="logo" src="../assets/images/logo2.png" alt="">
                                </div>
                        </div>
                        
                        <div class="right-cont fl clearfix">
                                <div class="search-box fr">
                                    <div class="ipt-box" @click="goSearch">
                                        <input class="ipt" type="text" placeholder="搜索新闻资讯" disabled>
                                        <input class="btn" type="button" value="搜索">
                                    </div>
                                </div>
                                <div class="login fr"> 
                                    <el-button type="text" @click="dialogTableVisibleRegister = true">注册</el-button>
                                    <el-dialog class="dlog-regi-login" title="注册" :visible.sync="dialogTableVisibleRegister">
                                        <form action="" class="register-login-form">
                                            <div class="demo-input-suffix">
                                                <el-input class="ipt"  placeholder="请输入手机号" prefix-icon="el-icon-user" v-model="input1"></el-input>
                                                <el-input class="ipt"  placeholder="请输入邮箱" prefix-icon="el-icon-message" v-model="input2"></el-input>
                                                <el-input class="ipt"  placeholder="请输入密码" prefix-icon="el-icon-lock" v-model="input3" show-password></el-input>
                                                </div>
                                            <input class="btn" type="button" value="注册">
                                        </form>
                                    </el-dialog>
                                    
                                </div>
                                <div class="register fr">
                                    <el-button type="text" @click="dialogTableVisibleLogin = true">登录</el-button>
                                    <el-dialog class="dlog-regi-login" title="登录" :visible.sync="dialogTableVisibleLogin">
                                        <form action="" class="register-login-form">
                                            <div class="demo-input-suffix">
                                                <el-input class="ipt"  placeholder="请输入手机号" prefix-icon="el-icon-user" v-model="input4"></el-input>
                                                <el-input class="ipt"  placeholder="请输入密码" prefix-icon="el-icon-lock" v-model="input5" show-password></el-input>
                                            </div>
                                            <input class="btn" type="button" value="登录">
                                        </form>
                                    </el-dialog>
                                </div>
                        </div>
                    </div>
            </div>
        </div>

        <div class="detail-content clearfix ">
            <div class="detail-box-left fl">
                <div class="box-title">
                    <h2 class="d-title">{{DetailData.title}}</h2>
                    <div class="d-tags">{{DetailData.tags}}</div>
                    <div class="d-tips">
                        <span>来源: <a href="">{{DetailData.posterNickName}}</a></span>
                        <span>{{DetailData.publishTime}}</span>
                        <span>评论：{{DetailData.commentCount}}</span>
                    </div>
                </div>

                <div class="box-cont">
                    <h3 class="c-title">{{DetailData.title}}</h3>
                    <!-- <img class="c-img auto-img" :src="DetailData.images" alt=""> -->
                    <img class="c-img auto-img" :src="DetailData.images[0].src" alt="">
                    <!-- v-html去除标签，整理文本格式 -->
                    <div class="c-texts" v-html="DetailData.content">{{DetailData.content}}</div>
                </div>   

                <div class="box-comment">
                    <h3 class="com-title">冲浪评论区</h3>
                    <div class="comment-area">
                        <textarea class="texta" name="" id="" cols="30" rows="6" placeholder="留下我的足迹。。。"></textarea>
                        <button class="btn fr">发布</button>
                    </div>
                    <div class="comment-item" v-for="(item,index) in CommentData" :key="index">
                        <div class="item-top clearfix">
                            <div class="i-left fl clearfix">
                                <img class="avatars fl" :src="item.user.avatar" alt="">
                                <div class="nickname fl">{{item.user.nickname}}</div>
                            </div>
                            <div class="i-right fr">
                                <span class="location">{{item.user.location}}</span>
                                <span class="date">{{item.createTime}}</span>
                            </div>
                        </div>
                        <div class="item-center">{{item.content}}</div>
                        <div class="item-bottom clearfix">
                            <span class="say fr"><a href="#">回复</a></span>
                            <span class="enjoy fr"><a href="#">分享</a>[{{item.shareCount}}]</span>
                        </div>
                    </div>
                    <div class="pagination-page">
                    <el-pagination 
                    class="pag-box" 
                    background 
                    layout="prev, pager, next" 
                    :size-change="10"
                    :total="1000"
                    :current-change = "currentPage"
                    @current-change="handleCurrentChange"
                    >
                    </el-pagination>
                </div>
                  
                </div> 
            </div>  

            <div class="detail-box-right fl">
                <div class="ads-box-one">
                    <el-carousel height="150px">
                        <el-carousel-item v-for="item in adsBoxOneData" :key="item">
                            <img class="item-img" :src="item" alt="">
                        </el-carousel-item>
                    </el-carousel>
                </div>

                <div class="ads-box-two">
                    <a v-if="adshowimg" target="_blank" href="https://www.opposhop.cn/">
                        <img class="ad-img auto-img" src="https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1597660738154&di=adb3affb54749917875a4de29493285a&imgtype=0&src=http%3A%2F%2Fhbimg.b0.upaiyun.com%2F0d855edf6e8ee84b0148f9c2a1685da05536710311536c-x5PCxc_fw236" border="0"  alt="">
                    </a>
                    <a v-else target="_blank" href="http://cdn.17qcad.cn/pc/24808/xinbaojun/">
                        <img class="ad-img auto-img" src="https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1597660738156&di=ffaa2ce5c7b6afa7ed893f8ed6562550&imgtype=0&src=http%3A%2F%2Ftuddimgs.oss-cn-hangzhou.aliyuncs.com%2F202002%2FDesign006_pAEM7y6jMk.jpg%3Fx-oss-process%3Dstyle%2Fprev_w_750_h_auto" border="0" alt="">
                    </a>
                    <div class="ad-text">广告</div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import '../assets/css/detail.less';
    export default {
        data(){
            return{
                input: '',
                dialogTableVisibleRegister:false,
                dialogTableVisibleLogin:false,
                input1:'',
                input2:'',
                input3:'',
                input4:'',
                input5:'',
                adsBoxOneData:[
                    'https://ss0.bdstatic.com/70cFuHSh_Q1YnxGkpoWK1HF6hhy/it/u=3469959579,630262573&fm=26&gp=0.jpg',
                    'https://ss1.bdstatic.com/70cFuXSh_Q1YnxGkpoWK1HF6hhy/it/u=1920009747,1370755904&fm=26&gp=0.jpg',
                    'https://ss3.bdstatic.com/70cFv8Sh_Q1YnxGkpoWK1HF6hhy/it/u=2869800889,4003075368&fm=26&gp=0.jpg'
                ],
                num:60,
                cleardata:null,
                adshowimg:true,
                id:[],
                DetailData:[],
                str:[],
                CommentData:[],

                currentPage:1,
                // 定义页数
                pageNum:'',
               
            }
        },
        created(){
            this.id = this.$route.query.id;
            this.getDetailData(this.id);
             this.getCommentData(this.id);

             // 倒计时
            this.cleardata = setInterval(()=>{
                if(this.num == 40){
                   this.adshowimg = false;
                   this.num--;
                    
                }else{
                    this.num--;
                }
            },1000)

        },
        
        methods:{

           getDetailData(id){
               this.axios({
                  method: 'GET',
                  url: '/detail',
                  params:{
                       apikey: this.appkey,
                       itemId:id,
                   }
               }).then((result) =>{
                //    console.log('result',result);
                   this.DetailData = result.data.data;

               }).catch((err) =>{
                    // console.log('err',err);
               })
           },

             getCommentData(id,pageNum){
               this.axios({
                  method: 'GET',
                  url: '/comment',
                  params:{
                       apikey: this.appkey,
                        itemId:id,
                        page:pageNum,
                   }
               }).then((result) =>{
                //    console.log('result',result);
                   this.CommentData = result.data.data;
                  
               }).catch((err) =>{
                    // console.log('err',err);
               })
           },

         //点击页码，获取对应的val值，在传给要显示的页数内容
        handleCurrentChange(val){
            
            // console.log('val',val)
            let pageNum = val;
            // console.log('pageNum',pageNum)
            //调用请求数据，才能获取值
            this.getCommentData(pageNum)
        },
        goMain(){
            this.$router.push({name:'Sole'});
        },
      
        goSearch(){
            this.$router.push({name:'Search'});
        },

        //销毁函数
        beforeDestroy(){
            
            // 关闭定时器
            clearInterval(this.cleardata);
        },
        }

    }
</script>

<style lang="less" scoped>

</style>